.dock {
    position: absolute;
    top: 12px;
    left: 50%;
    transform: translateX(-20%);
    align-items: flex-end;
    height: 58px;
    display: flex;
    padding: 10px;
    padding-bottom: 6px;
    gap: 12px;
    /* background-color: rgba(0, 0, 0, 0.92); */
    will-change: contents;
    box-sizing: content-box;
    border-radius: 12px;
    transform-origin: center bottom;
  }
  